<template>
  <div class="search-bar">
    <div class="search-header">
      <div class="input-container">
        <div class="image">
          <img src="../../assets/sousou.png" />
          <div class="input-con">
            <input type="text" class="text-sty" placeholder=''>
          </div>
        </div>
      </div>
      <div class="cancel-btn">
        
      </div>
    </div>
    <div class="search-title">
      
    </div>
    <div class="search-keywords">
      <div class="keyword" v-for='item in searchList' :key='item.id'>
        <router-link :to=item.keyName class="rou">
          <div>{{item.keyword}}</div>
        </router-link>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        searchList: []
      }
    },
    mounted() {
      this.getSearchList()
    },
    methods: {
      getSearchList() {
        this.$http.get('/api/searchdata').then(res => {
          this.searchList = res.data.data
       })
      }
    }
  }

</script>

<style scoped>
  .search-bar {
    position: fixed;
    margin: 0;
    padding: 0;
    width: 100%;
  }

  .search-header {
    float: left;
    margin: 0;
    padding: 0;
    width: 98%;
    height: 50px;
    /* background-color: aqua; */
    -webkit-box-shadow: 0 0.04rem 0.08rem 0 rgba(0, 0, 0, .08);
    box-shadow: 0 0.04rem 0.08rem 0 rgba(0, 0, 0, .08);
  }

  .search-input {
    width: 100%;
    height: 50px;
    /* background-color: blue; */
  }

  .input-container {
    width: 300px;
    height: 32px;
    margin-left: 10px;
    border-radius: 1rem;
    margin-top: 10px;
    background-color: #e6e6e6;
  }

  .image img {
    float: left;
    margin-left: 16px;
    height: 24px;
    width: 24px;
    margin-top: 5px;
  }

  .text-sty {
    float: left;
    width: 220px;
    margin-left: 8px;
    height: 24px;
    margin-top: 4px;
    font-size: 16px;
    background-color: #e6e6e6;
    border: #e6e6e6;
  }

  .cancel-btn {
    float: right;
    margin-left: 8px;
    margin-top: -28px;
    width: 46px;
    height: 30px;
    font-size: 18px;
    color: #999;
  }

  .search-title {
    float: left;
    background-color: whitesmoke;
    font-size: 14px;
    width: 100%;
    height: 34px;
    margin-left: -4px;
    padding-left: 14px;
    padding-top: 12px;
    color: #999;
  }

  .search-keywords {
    float: left;
    width: 100%;
    height: 300px;
    /* background-color: rebeccapurple; */
    /* padding-top: 10px; */
    padding-left: 10px;
    /* margin-top: 5px; */
  }

  .keyword {
    float: left;
    background: whitesmoke;
    border: #999 1 solid;
    width: 80px;
    height: 30px;
    margin-top: 20px;
    margin-right: 10px;
    text-align: center;
    padding-top: 8px;
    font-size: 14px;
    border-radius: .4rem;
  }

  .rou {
    color: #999;
    text-decoration-line: none;
  }

</style>



