<template>
<div class="banner">
    <!-- tuijain -->
    <div class="swiper-container">
        <div class="swiper-wrapper content">
            <div class="swiper-slide" v-for="item in bannerList" v-bind:key='item.id'>
                <img :src="item.img" />
            </div>
        </div>
        <div class="swiper-pagination"></div>
    </div>
    <sections></sections>
    <content-sec></content-sec>
</div>
</template>

<script>
    import '../../../static/css/swiper.min.css';
	import Swiper from '../../../static/js/swiper.min';
    import Sections from './sections'
    import ContentSec from './contentSec'
export default{
    data(){
        return{
            bannerList:[],

        }
    },
    mounted(){
        setTimeout( ()=> {
            const swiper = new Swiper('.swiper-container',{
                pagination: '.swiper-pagination',
                paginationClickable: true,
                autoplay: 1000,
                effect: 'fade'
            })
        },1000)
        this.getBannerList();
    },
    methods: {
        getBannerList(){
            this.$http.get('/api/bannerdata').then( res => {
                this.bannerList = res.data.data
                // console.log(this.bannerList)
            })
          
        }
    },
    components: {
        Sections,
        ContentSec
    }
}

</script>

<style scoped>
.banner{
    margin-top: 10px;
    z-index: 9;
    width: 100%;
    /* height: 380px; */
    margin-left: 4px;
    /* background-color: aqua; */
   
}
img{
    width: 100%;
    height: 100%;
}
.swiper-container{
    border-radius: .6rem;
}
</style>



