<template>
  <div class="container">
    <div class="filters ">
      <div class="content"  v-for='item in  searchList' :key='item.id'>
        <div class="text-con">
          <router-link :to=item.keyName>
            <p class="text" >{{item.keyword}}</p>
          </router-link>
        </div>
      </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
   data(){
     return{
       searchList:[]
     }
   },
   mounted(){
     this.getSearchList()
   },
   methods:{
    getSearchList(){
      this.$http.get('/api/searchdata').then( res =>{
        this.searchList = res.data.data
      })
    }
   }
  }

</script>

<style scoped>
  .filters {
    z-index: 2;
    padding-top: 60px;
    width: 100%;
    margin-left: 7px;
    width: 375px;
    height: 34px;
  }

  .content {
    display: inline-block;
    width: 60px;
    height: 22px;
    margin-right:10px;
    border: 1px solid gainsboro;
     border-radius: 0.4rem;
  }

  .text {
    position: absolute;
    font-size: 13px;
    margin-top: 2px;
    margin-left: 16px;
    color: gainsboro;
  }

 .text:active{
   color: black;
 }
</style>



