<template>
  <div class="big-container">
    <div class="title">
      
    </div>
    <div class="content" v-for='item in contentList' :key='item.id'>
        <router-link :to=item.imgLink>
        <div class="image">
          <img :src="item.img" alt="">
        </div>
    </router-link>
      <div class="content-text">
        {{item.content}}
      </div>
      <div class="content-tip">
        <div class="user-img">
          <img :src="item.userImg" alt="">
        </div>
        <div class="user-name">{{item.userName}}</div>
        <div class="user-heart">
          <img src="../../assets/aixin.png">
        </div>
        <div class="num">{{item.num}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        contentList: []
      }
    },
    mounted() {
      this.getContentList()
    },
    methods:{
        getContentList(){
        this.$http.get('/api/searchdata').then(res => {
          this.contentList = res.data.data
          // console.log(this.contentList)
        })
      }
    }

  }

</script>

<style scoped>
  .big-container {

    width: 100%;
    height: 900px;
    }

  .title {
    background-color: white;
    border: 1px solid white;
    float: left;
    margin-top: 10px;
    font-size: 16px;
    padding-left: 14px;
    font-weight: 500;
    padding-top: 10px;
    color: #333;
    height: 30px;
    width: 100%;
    /* background-color: bisque; */
  }

  .content {
    float: left;
    z-index: 3;
    /* position: absolute; */
    /* margin-top: 50px; */
    margin-left: 5px;
    width: 173px;
    height: 265px;
    background-color: white;
    border: 1px solid white;
  }

  .image {
    width: 172.5px;
    height: 172.5px;
  }

  img {
    border-radius: .3rem;
    width: 100%;
    height: 100%;
  }

  .content-text {
    margin-top: 10px;
    font-size: 14px;
    color: #666;
    width: 152.5px;
    height: 37px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-left: .1rem;
  }

  .user-img {
    float: left;
    /* margin-top: 5px; */
    width: 25px;
    height: 25px;
  }

  .user-img img {
    border-radius: 50%;
  }

  .user-name {
    float: left;
    margin-top: 4px;
    color: #333;
    font-size: .22rem;
    width: 80px;
    height: 25px;
    display: inline-block;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: top;
    margin-left: .5rem;
  }

  .user-heart img {
    margin-top: 6px;
    float: left;
    margin-left: 4px;
    width: 15px;
    height: 15px;
  }

  .num {
    float: left;
    /* position: absolute; */
    margin-top: 8px;
    margin-left: 4px;
    right: .2rem;
    color: #666;
    font-size: .9rem;
    height: .5rem;
    line-height: .5rem;
  }

</style>



