<template>
  <div class="sections">
    <div class="title">
      <h3 class="section-title"></h3>
      <a href="" class="show-more"></a>
    </div>
    <div class="page-images" >
      <div class="page-image"  v-for='item in sectionList' :key='item.id'>
       <img :src='item.image'/>
       <div class="page-cext">
        <h4 class="page-name">{{item.name}}</h4>
        <p class="page-title">{{item.title}}</p>
      </div>
    </div>
</div>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        sectionList:[]
      }
     },
    mounted(){
      this.getSectionList()
    },
    methods:{
      getSectionList(){
        this.$http.get('/api/sectiondata').then( res => {
          this.sectionList = res.data.data
          // console.log(this.sectionList)
        })
      }
   }
  }

</script>
<style scoped>
  .sections {
    margin-top: 10px;
    background-color: white;
    z-index: 10;
    float: left;
    /* background-color: silver; */
    width: 100%;
    height: 180px;
    padding-top: 20px;
  }

  .section-title {
    float: left;
    margin-top: 0;
    margin-left: 14px;
    font-size: 16px;
    line-height: .34rem;
    font-weight: 500;
    color: #333;
  }

  .show-more {
    float: right;
    margin-right: 16px;
    font-size: .26rem;
    line-height: .34rem;
    font-weight: 400;
    color: #999;
  }

  a {
    text-decoration: none;
  }

  .page-images {
    /* float: left; */
    margin-top: 26px;
    /* background-color: darkblue; */
    height: 152px;
  }

  .page-image {
    float: left;
    /* background-color: aqua; */
    margin-left: 10px;
    width: 165px;
    height: 100px;
  }

  img {
    border-radius: .4rem;
    width: 100%;
    height: 100%;
  }
.page-name {
    float: left;
     font-size: 15px;
    color: #333;
      height: .3rem;
    margin: 0;
    margin-left: 8px;

}
.page-title{
    float: left;
    font-size: .22rem;
    color: #999;
    line-height: .26rem;
    height: .26rem;
    margin-top: 21px;
    margin-left: 8px;
}
</style>



